import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { favoritesContext } from './favoritesContext';

function ProductPage() {

    const [products, setproducts] = useState([]);
    const { favorites, setfavorites } = useContext(favoritesContext);

    useEffect(() => {

        fetch('https://northwind.vercel.app/api/products')
            .then(res => res.json())
            .then(data => setproducts(data));

    }, []);

    let navigate = useNavigate();

    const addToFavorites = (item) => {

        //context zerindeki global state e bir favori ekleyecek

        //eer bu rn favorilerde varsa ekleme!
        let favoriteProduct = favorites.find(q => q.id == item.id);

        if (!favoriteProduct) {
            setfavorites([...favorites, item])
        }

    }

    const isFavorite = (id) => {


        let favoriProduct = favorites.find(q => q.id == id);

        if (favoriProduct) {
            return true;
        }
        else {
            return false;
        }

    }


    const goDetail = (name, id) => {

        name = convertToSlug(name);
        navigate('/products/' + id + "/" + name);

    }

    function convertToSlug(Text) {
        return Text
            .toLowerCase()
            .replace(/ /g, '-')
            .replace(/[^\w-]+/g, '')
            ;
    }

    return (<>
        <table>
            <thead>
                <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>Unit Price</th>
                    <th>Favorite</th>
                </tr>
            </thead>
            <tbody>
                {
                    products && products.map((item, key) => {
                        return <tr key={key}>
                            <td>{item.id}</td>
                            <td>{item.name}</td>
                            <td>{item.unitPrice}</td>
                            <td>
                                {

                                    isFavorite(item.id) == true ?
                                        <button onClick={() => addToFavorites(item)}>Remove Favorites</button>
                                        :
                                        <button onClick={() => addToFavorites(item)}>Add To Favorites</button>
                                }
                            </td>
                            <td><button onClick={() => goDetail(item.name, item.id)}>Go to detail</button></td>

                        </tr>
                    })
                }
            </tbody>

        </table>
    </>
    )
}

export default ProductPage